import React from "react";
import { Row, Column } from "components/Grid.tsx";

import ApplyButton from "components/reusable/atoms/buttons/ApplyButton";

import checkboxIcon from "assets/img/icons/orange/checkbox-checked.svg";
import lockIcon from "assets/img/icons/orange/lock.svg";
import cardIcon from "assets/img/icons/orange/card-management.svg";
import {
  Container,
  ContentWrapper,
  Explanation,
  ExplanationTitle,
  ExplanationBody,
  DesktopCTA,
  CardList,
  Card,
  CardTitle,
  CardIcon,
  CardBody,
  MobileCTA,
} from "./elements";

const cards = [
  {
    title: "STEP 1",
    icon: checkboxIcon,
    body: "Sign up in minutes",
  },
  {
    title: "STEP 2",
    icon: lockIcon,
    body: "Connect to your bank",
  },
  {
    title: "STEP 3",
    icon: cardIcon,
    body: "Start issuing virtual cards",
  },
];

const CardComponent = ({ title, icon, body }) => (
  <Card>
    <CardTitle>{title}</CardTitle>
    <CardIcon src={icon} />
    <CardBody>{body}</CardBody>
  </Card>
);

const CardListComponent = ({ cards }) => (
  <CardList>
    {cards.map((cardProps) => (
      <CardComponent key={cardProps.title} {...cardProps} />
    ))}
  </CardList>
);

const QualifySection = ({ analyticsPath, companyType }) => (
  <Container>
    <Row padding={30} gutterWidth={26} verticalAlignment="stretch">
      <Column span={1} spanMedium={0} />
      <Column span={10} spanMedium={12}>
        <ContentWrapper>
          <Explanation>
            <ExplanationTitle>How do I know if I qualify?</ExplanationTitle>
            <ExplanationBody>
              {
                "Our underwriting technology helps us see if your company is the right fit for a Brex card the moment you connect to our systems."
              }
              <br />
              <br />
              {
                "Companies that have healthy cash balances or sales are all great candidates for the Brex card."
              }
            </ExplanationBody>
            <DesktopCTA>
              <ApplyButton
                companyType={companyType}
                analyticsPath={analyticsPath}
              >
                SEE IF YOU QUALIFY
              </ApplyButton>
            </DesktopCTA>
          </Explanation>
          <CardListComponent cards={cards} />
          <MobileCTA>
            <ApplyButton
              companyType={companyType}
              analyticsPath={analyticsPath}
            >
              SEE IF YOU QUALIFY
            </ApplyButton>
          </MobileCTA>
        </ContentWrapper>
      </Column>
      <Column span={1} spanMedium={0} />
    </Row>
  </Container>
);

export default QualifySection;
