import React from "react";
import { Container, Title, Subtitle } from "./elements";

const TextRow = ({
  title,
  subtitle,
  containerPadding,
  containerPaddingMedium,
  containerPaddingSmall,
}) => (
  <Container
    containerPadding={containerPadding}
    containerPaddingMedium={containerPaddingMedium}
    containerPaddingSmall={containerPaddingSmall}
  >
    <Title>{title}</Title>
    {subtitle && <Subtitle>{subtitle}</Subtitle>}
  </Container>
);

export default TextRow;
