import styled from "@emotion/styled";
import { margin, padding } from "polished";
import colors from "styles/colors";
import { gothamFonts } from "styles/fonts";
import { breakPoints } from "components/Grid.tsx";

export const Container = styled("div")(
  ({
    containerPadding = padding(90, 60),
    containerPaddingMedium = padding(60, 30),
    containerPaddingSmall = padding(30, 15),
  }) => [
    {
      width: "100%",
      display: "flex",
      flexDirection: "column",
      justifyContent: "center",
      alignItems: "center",
      backgroundColor: colors.white,
      ...containerPadding,

      [breakPoints.medium]: {
        alignItems: "flex-start",
        width: "100%",
      },
    },
    containerPaddingMedium !== undefined && {
      [breakPoints.medium]: {
        ...containerPaddingMedium,
      },
    },
    containerPaddingSmall !== undefined && {
      [breakPoints.small]: {
        ...containerPaddingSmall,
      },
    },
  ],
);

export const Title = styled("h2")({
  margin: 0,
  color: colors.black,
  maxWidth: 900,
  textAlign: "center",
  ...gothamFonts.h2,

  [breakPoints.medium]: {
    width: "100%",
    textAlign: "left",
  },

  [breakPoints.small]: {
    ...gothamFonts.h3,
  },
});

export const Subtitle = styled("p")({
  // need to explicitly set since marginBottom has a default value
  ...margin(36, 0, 0, 0),
  color: colors.black,
  maxWidth: 720,
  textAlign: "center",
  ...gothamFonts["body-l"],
  fontWeight: 300,

  [breakPoints.medium]: {
    width: "100%",
    textAlign: "left",
    ...gothamFonts["body-m"],
    fontWeight: 300,
  },
});
