import React from "react";
import { compose, withStateHandlers } from "recompose";

import { Row, Column } from "components/Grid.tsx";
import ArrowDown from "assets/img/icons/orange/arrow-down.svg";
import { Question, Answer, Wrapper, Arrow } from "./elements";

const FAQItem = ({ answer, question, isOpen, toggle }) => (
  <Row padding={30} gutterWidth={30}>
    <Column span={1} />
    <Column span={10}>
      <Wrapper onClick={toggle}>
        <Arrow src={ArrowDown} alt="a" flipped={isOpen} />
        <Question isOpen={isOpen}>{question}</Question>
      </Wrapper>
      <Answer isOpen={isOpen}>{answer}</Answer>
    </Column>
    <Column span={1} />
  </Row>
);

const initialState = () => ({
  isOpen: false,
});

const _stateHandlers = {
  toggle: ({ isOpen }) => () => ({
    isOpen: !isOpen,
  }),
};

export default compose(withStateHandlers(initialState, _stateHandlers))(
  FAQItem,
);
